import React from 'react';
import styles from './styles.module.css';
import Team from '../../models/Team';
import emptyTeam from '../../consts/emptyTeam';
import TeamForm from '../TeamForm';


type RegistrationPageProps = {
    history: {
        push: (url: string) => void;
    };
    updateTeam: (team: Team) => void;
}

export default function RegistrationPage(props: RegistrationPageProps) {
    const registerTeam = (team: Team) => {
        props.updateTeam(team);
    }
    
    return (
        <div className={ styles.root }>
            <h2>Early Team Registration</h2>
            <div className={ styles.disclaimer }>
                <p>We encourage to register your team before the start of the contest.</p>
                <p>You'll be able to check the <a
                    href="https://github.com/icfpcontest2020/dockerfiles/blob/master/README.md" target="_blank"
                    rel="noopener noreferrer">submission system</a> and make sure that everything goes smoothly
                    during the contest.</p>
                <p>As part of registration, we kindly ask to create a private Git repository for your team and provide its
                    SSH URL.</p>
                <p>Don't worry, all information you enter here can be updated later.</p>
            </div>
            <TeamForm startValue={ emptyTeam } onSubmit={ registerTeam }/>
        </div>
    )
}
